"use client"

/**
 * ============================================================================
 * VUA TING VIT - GAME COMPONENT
 * ============================================================================
 * 
 * Giao din chnh ca game  ch "Vua Ting Vit"
 * 
 * ============================================================================
 */

import { useState } from "react"
import { useCurrentAccount } from "@iota/dapp-kit"
import { useVuaTiengViet, Question } from "@/hooks/useVuaTiengViet"
import { Button, Container, Flex, Heading, Text, TextField, Card, Badge, Dialog, TextArea } from "@radix-ui/themes"
import ClipLoader from "react-spinners/ClipLoader"

// ============================================================================
// QUESTION CARD COMPONENT
// ============================================================================

interface QuestionCardProps {
    question: Question
    onAnswer: (questionId: string, answer: string, salt: string) => void
    onCancel: (questionId: string) => void
    currentAddress?: string
    isLoading: boolean
}

const QuestionCard = ({ question, onAnswer, onCancel, currentAddress, isLoading }: QuestionCardProps) => {
    const [showAnswerDialog, setShowAnswerDialog] = useState(false)
    const [answer, setAnswer] = useState("")
    const [salt, setSalt] = useState("")

    const isCreator = currentAddress?.toLowerCase() === question.creator.toLowerCase()
    const rewardInIOTA = (question.rewardAmount / 1_000_000_000).toFixed(4)

    const handleSubmitAnswer = () => {
        onAnswer(question.id, answer, salt)
        setShowAnswerDialog(false)
        setAnswer("")
        setSalt("")
    }

    return (
        <Card style={{ marginBottom: "1rem", padding: "1.5rem" }}>
            <Flex justify="between" align="start" style={{ marginBottom: "1rem" }}>
                <div style={{ flex: 1 }}>
                    <Flex gap="2" align="center" style={{ marginBottom: "0.5rem" }}>
                        <Heading size="4">{question.questionText || "Cu hi"}</Heading>
                        {question.isActive ? (
                            <Badge color="green">ang m</Badge>
                        ) : (
                            <Badge color="gray"> ng</Badge>
                        )}
                    </Flex>

                    {question.hint && (
                        <Text size="2" style={{ color: "var(--gray-11)", display: "block", marginBottom: "0.5rem" }}>
                             Gi : {question.hint}
                        </Text>
                    )}
                </div>

                <div style={{ textAlign: "right" }}>
                    <Text size="5" weight="bold" style={{ color: "var(--accent-11)" }}>
                         {rewardInIOTA} IOTA
                    </Text>
                </div>
            </Flex>

            <Flex gap="2" style={{ marginTop: "1rem" }}>
                {question.isActive && (
                    <Dialog.Root open={showAnswerDialog} onOpenChange={setShowAnswerDialog}>
                        <Dialog.Trigger>
                            <Button color="green" disabled={isLoading}>
                                Tr li
                            </Button>
                        </Dialog.Trigger>

                        <Dialog.Content style={{ maxWidth: 450 }}>
                            <Dialog.Title>Tr li cu hi</Dialog.Title>
                            <Dialog.Description size="2" mb="4">
                                Nhp p n v salt (nu bit). Nu ng, bn s nhn c {rewardInIOTA} IOTA ngay lp tc!
                            </Dialog.Description>

                            <Flex direction="column" gap="3">
                                <label>
                                    <Text as="div" size="2" mb="1" weight="bold">
                                        p n *
                                    </Text>
                                    <TextField.Root
                                        value={answer}
                                        onChange={(e) => setAnswer(e.target.value)}
                                        placeholder="Nhp p n ca bn"
                                    />
                                </label>

                                <label>
                                    <Text as="div" size="2" mb="1" weight="bold">
                                        Salt *
                                    </Text>
                                    <TextField.Root
                                        value={salt}
                                        onChange={(e) => setSalt(e.target.value)}
                                        placeholder="Nhp salt (c cung cp bi ngi to cu hi)"
                                    />
                                    <Text size="1" style={{ color: "var(--gray-10)" }}>
                                        Salt l m b mt  xc minh p n
                                    </Text>
                                </label>
                            </Flex>

                            <Flex gap="3" mt="4" justify="end">
                                <Dialog.Close>
                                    <Button variant="soft" color="gray">
                                        Hy
                                    </Button>
                                </Dialog.Close>
                                <Button
                                    onClick={handleSubmitAnswer}
                                    disabled={!answer || !salt || isLoading}
                                    color="green"
                                >
                                    {isLoading ? <ClipLoader size={16} /> : "Gi p n"}
                                </Button>
                            </Flex>
                        </Dialog.Content>
                    </Dialog.Root>
                )}

                {question.isActive && isCreator && (
                    <Button
                        color="red"
                        variant="soft"
                        onClick={() => onCancel(question.id)}
                        disabled={isLoading}
                    >
                        Hy cu hi
                    </Button>
                )}

                {question.winner && (
                    <Badge color="gold" size="2">
                         Ngi thng: {question.winner.slice(0, 8)}...
                    </Badge>
                )}
            </Flex>

            <Text size="1" style={{ color: "var(--gray-10)", marginTop: "1rem", display: "block" }}>
                ID: {question.id.slice(0, 16)}... | Ngi to: {question.creator.slice(0, 8)}...
            </Text>
        </Card>
    )
}

// ============================================================================
// MAIN GAME COMPONENT
// ============================================================================

const VuaTiengVietGame = () => {
    const currentAccount = useCurrentAccount()
    const {
        address,
        gameStateId,
        questions,
        gameStats,
        transactionState,
        createQuestion,
        submitAnswer,
        cancelQuestion,
        saveGameStateId,
        fetchQuestions,
    } = useVuaTiengViet()

    // Form state cho to cu hi
    const [showCreateDialog, setShowCreateDialog] = useState(false)
    const [newQuestion, setNewQuestion] = useState("")
    const [newHint, setNewHint] = useState("")
    const [newAnswer, setNewAnswer] = useState("")
    const [newSalt, setNewSalt] = useState("")
    const [newReward, setNewReward] = useState("")

    // Config state
    const [showConfigDialog, setShowConfigDialog] = useState(false)
    const [tempGameStateId, setTempGameStateId] = useState("")

    const isConnected = !!currentAccount

    // To random salt
    const generateSalt = () => {
        const chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789"
        let result = ""
        for (let i = 0; i < 16; i++) {
            result += chars.charAt(Math.floor(Math.random() * chars.length))
        }
        setNewSalt(result)
    }

    const handleCreateQuestion = async () => {
        if (!newQuestion || !newAnswer || !newSalt || !newReward) return

        const rewardInNanos = Math.floor(parseFloat(newReward) * 1_000_000_000)
        await createQuestion(newQuestion, newHint, newAnswer, newSalt, rewardInNanos)

        if (!transactionState.error) {
            setShowCreateDialog(false)
            setNewQuestion("")
            setNewHint("")
            setNewAnswer("")
            setNewSalt("")
            setNewReward("")
        }
    }

    const handleSaveConfig = () => {
        if (tempGameStateId) {
            saveGameStateId(tempGameStateId)
            setShowConfigDialog(false)
        }
    }

    if (!isConnected) {
        return (
            <div style={{
                minHeight: "100vh",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                padding: "1rem",
                background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)"
            }}>
                <Card style={{ maxWidth: "500px", width: "100%", padding: "2rem", textAlign: "center" }}>
                    <Heading size="7" style={{ marginBottom: "1rem" }}> Vua Ting Vit</Heading>
                    <Text size="3" style={{ color: "var(--gray-11)" }}>
                        Game  ch ting Vit trn blockchain IOTA
                    </Text>
                    <Text style={{ marginTop: "1.5rem", display: "block" }}>
                        Vui lng kt ni v  chi game.
                    </Text>
                </Card>
            </div>
        )
    }

    return (
        <div style={{ minHeight: "100vh", padding: "1rem", background: "var(--gray-a2)" }}>
            <Container style={{ maxWidth: "900px", margin: "0 auto" }}>
                {/* Header */}
                <Flex justify="between" align="center" style={{ marginBottom: "2rem" }}>
                    <div>
                        <Heading size="7"> Vua Ting Vit</Heading>
                        <Text size="2" style={{ color: "var(--gray-11)" }}>
                            Game  ch - on ng nhn thng t ng!
                        </Text>
                    </div>

                    <Flex gap="2">
                        <Dialog.Root open={showConfigDialog} onOpenChange={setShowConfigDialog}>
                            <Dialog.Trigger>
                                <Button variant="soft"> Cu hnh</Button>
                            </Dialog.Trigger>

                            <Dialog.Content style={{ maxWidth: 450 }}>
                                <Dialog.Title>Cu hnh Game</Dialog.Title>
                                <Dialog.Description size="2" mb="4">
                                    Nhp Game State ID sau khi deploy contract.
                                </Dialog.Description>

                                <label>
                                    <Text as="div" size="2" mb="1" weight="bold">
                                        Game State ID
                                    </Text>
                                    <TextField.Root
                                        value={tempGameStateId}
                                        onChange={(e) => setTempGameStateId(e.target.value)}
                                        placeholder="0x..."
                                    />
                                </label>

                                <Flex gap="3" mt="4" justify="end">
                                    <Dialog.Close>
                                        <Button variant="soft" color="gray">Hy</Button>
                                    </Dialog.Close>
                                    <Button onClick={handleSaveConfig}>Lu</Button>
                                </Flex>
                            </Dialog.Content>
                        </Dialog.Root>

                        <Dialog.Root open={showCreateDialog} onOpenChange={setShowCreateDialog}>
                            <Dialog.Trigger>
                                <Button color="green" disabled={!gameStateId}> To cu hi</Button>
                            </Dialog.Trigger>

                            <Dialog.Content style={{ maxWidth: 500 }}>
                                <Dialog.Title>To cu hi mi</Dialog.Title>
                                <Dialog.Description size="2" mb="4">
                                    To cu hi  ch. p n s c m ha, khng ai c th sa sau khi to!
                                </Dialog.Description>

                                <Flex direction="column" gap="3">
                                    <label>
                                        <Text as="div" size="2" mb="1" weight="bold">Cu hi *</Text>
                                        <TextArea
                                            value={newQuestion}
                                            onChange={(e) => setNewQuestion(e.target.value)}
                                            placeholder="Nhp cu hi  ch..."
                                        />
                                    </label>

                                    <label>
                                        <Text as="div" size="2" mb="1" weight="bold">Gi </Text>
                                        <TextField.Root
                                            value={newHint}
                                            onChange={(e) => setNewHint(e.target.value)}
                                            placeholder="Nhp gi  (khng bt buc)"
                                        />
                                    </label>

                                    <label>
                                        <Text as="div" size="2" mb="1" weight="bold">p n *</Text>
                                        <TextField.Root
                                            value={newAnswer}
                                            onChange={(e) => setNewAnswer(e.target.value)}
                                            placeholder="Nhp p n ng"
                                        />
                                        <Text size="1" style={{ color: "var(--red-10)" }}>
                                             Lu : Sau khi to, bn KHNG TH sa p n!
                                        </Text>
                                    </label>

                                    <label>
                                        <Text as="div" size="2" mb="1" weight="bold">Salt *</Text>
                                        <Flex gap="2">
                                            <TextField.Root
                                                value={newSalt}
                                                onChange={(e) => setNewSalt(e.target.value)}
                                                placeholder="M b mt  xc minh"
                                                style={{ flex: 1 }}
                                            />
                                            <Button variant="soft" onClick={generateSalt}> Random</Button>
                                        </Flex>
                                        <Text size="1" style={{ color: "var(--gray-10)" }}>
                                            Chia s salt ny cho ngi chi  h c th xc minh p n.
                                        </Text>
                                    </label>

                                    <label>
                                        <Text as="div" size="2" mb="1" weight="bold">Tin thng (IOTA) *</Text>
                                        <TextField.Root
                                            type="number"
                                            value={newReward}
                                            onChange={(e) => setNewReward(e.target.value)}
                                            placeholder="S IOTA thng cho ngi on ng"
                                        />
                                    </label>
                                </Flex>

                                <Flex gap="3" mt="4" justify="end">
                                    <Dialog.Close>
                                        <Button variant="soft" color="gray">Hy</Button>
                                    </Dialog.Close>
                                    <Button
                                        onClick={handleCreateQuestion}
                                        disabled={!newQuestion || !newAnswer || !newSalt || !newReward || transactionState.isLoading}
                                        color="green"
                                    >
                                        {transactionState.isLoading ? <ClipLoader size={16} /> : "To cu hi"}
                                    </Button>
                                </Flex>
                            </Dialog.Content>
                        </Dialog.Root>
                    </Flex>
                </Flex>

                {/* Game Stats */}
                {gameStats && (
                    <Card style={{ marginBottom: "2rem", padding: "1.5rem" }}>
                        <Heading size="4" style={{ marginBottom: "1rem" }}> Thng k</Heading>
                        <Flex gap="4">
                            <div>
                                <Text size="6" weight="bold">{gameStats.totalQuestions}</Text>
                                <Text size="2" style={{ display: "block", color: "var(--gray-11)" }}>Tng cu hi</Text>
                            </div>
                            <div>
                                <Text size="6" weight="bold" style={{ color: "var(--green-11)" }}>{gameStats.totalSolved}</Text>
                                <Text size="2" style={{ display: "block", color: "var(--gray-11)" }}> gii</Text>
                            </div>
                            <div>
                                <Text size="6" weight="bold" style={{ color: "var(--accent-11)" }}>
                                    {(gameStats.totalRewardsDistributed / 1_000_000_000).toFixed(2)}
                                </Text>
                                <Text size="2" style={{ display: "block", color: "var(--gray-11)" }}>IOTA  pht</Text>
                            </div>
                        </Flex>
                    </Card>
                )}

                {/* Config Warning */}
                {!gameStateId && (
                    <Card style={{ marginBottom: "2rem", padding: "1.5rem", background: "var(--yellow-a3)" }}>
                        <Text style={{ color: "var(--yellow-11)" }}>
                             Cha cu hnh Game State ID. Nhn nt &quot;Cu hnh&quot;  nhp Game State ID sau khi deploy contract.
                        </Text>
                    </Card>
                )}

                {/* Transaction Status */}
                {transactionState.hash && (
                    <Card style={{ marginBottom: "1rem", padding: "1rem", background: "var(--green-a3)" }}>
                        <Text size="2" style={{ color: "var(--green-11)" }}>
                             Transaction: {transactionState.hash.slice(0, 20)}...
                            {transactionState.isConfirmed && " ( xc nhn)"}
                        </Text>
                    </Card>
                )}

                {transactionState.error && (
                    <Card style={{ marginBottom: "1rem", padding: "1rem", background: "var(--red-a3)" }}>
                        <Text style={{ color: "var(--red-11)" }}>
                             Li: {transactionState.error.message}
                        </Text>
                    </Card>
                )}

                {/* Questions List */}
                <Flex justify="between" align="center" style={{ marginBottom: "1rem" }}>
                    <Heading size="5"> Danh sch cu hi</Heading>
                    <Button variant="soft" onClick={fetchQuestions} disabled={transactionState.isLoading || !gameStateId}>
                        {transactionState.isLoading ? <ClipLoader size={16} /> : " Lm mi"}
                    </Button>
                </Flex>

                {transactionState.isLoading && questions.length === 0 ? (
                    <Card style={{ padding: "2rem", textAlign: "center" }}>
                        <ClipLoader size={32} />
                        <Text style={{ marginTop: "1rem", display: "block" }}>ang ti...</Text>
                    </Card>
                ) : questions.length === 0 ? (
                    <Card style={{ padding: "2rem", textAlign: "center" }}>
                        <Text style={{ color: "var(--gray-11)" }}>
                            Cha c cu hi no. Hy to cu hi u tin!
                        </Text>
                    </Card>
                ) : (
                    questions.map((question) => (
                        <QuestionCard
                            key={question.id}
                            question={question}
                            onAnswer={submitAnswer}
                            onCancel={cancelQuestion}
                            currentAddress={address}
                            isLoading={transactionState.isLoading}
                        />
                    ))
                )}

                {/* Footer */}
                <Text size="1" style={{ textAlign: "center", color: "var(--gray-10)", marginTop: "2rem", display: "block" }}>
                    Vua Ting Vit - Powered by IOTA Blockchain
                </Text>
            </Container>
        </div>
    )
}

export default VuaTiengVietGame
